html, body, div, span, h1, h2, h3, h4, h5, h6, p, em, img, strong, sub, sup, b, u, i, dl, dt, dd, ol, ul, li, fieldset, form, label, table, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  vertical-align: baseline;
  background: transparent;
  font-size: 100%; }

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td, td img {
  vertical-align: top; }

input, select, button, textarea {
  margin: 0;
  padding: 0;
  font-size: 100%; }

input[type="checkbox"] {
  vertical-align: middle; }

input[type="radio"] {
  vertical-align: text-bottom; }

sub {
  vertical-align: sub;
  font-size: smaller; }

sup {
  vertical-align: super;
  font-size: smaller; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

html {
  height: 100% !important;
  overflow-y: scroll; }

body {
  font-family: Arial, sans-serif normal 16px/1.5;
  font-weight: normal;
  text-align: left;
  color: #000;
  position: relative;
  width: 100%;
  height: 100%;
  background: #fff; }

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  color: red; }

body input:required:valid,
body textarea:required:valid {
  color: green; }

input, select, button, textarea {
  font-family: inherit;
  outline: none; }

label, input[type="button"], input[type="submit"], button {
  cursor: pointer;
  outline: none; }

a {
  color: #000; }

a, input, button, a img, textarea {
  transition: color 0.5s, background 0.5s, opacity 0.5s, border 0.5s;
  outline: none; }
  a:hover, a:focus, input:hover, input:focus, button:hover, button:focus, a img:hover, a img:focus, textarea:hover, textarea:focus {
    outline: none; }

img {
  width: auto;
  height: auto;
  max-width: 100%; }

.wrapper {
  margin: 0 auto;
  min-height: 100%;
  height: auto !important;
  height: 100%;
  position: relative;
  overflow: hidden; }

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato/Lato-Thin.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato/Lato-Thin.woff") format("woff"), url("../fonts/Lato/Lato-Thin.ttf") format("truetype"), url("../fonts/Lato/Lato-Thin.svg#Lato-Thin") format("svg");
  font-weight: 100;
  font-style: normal; }

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato/Lato-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato/Lato-Light.woff") format("woff"), url("../fonts/Lato/Lato-Light.ttf") format("truetype"), url("../fonts/Lato/Lato-Light.svg#Lato-Light") format("svg");
  font-weight: 300;
  font-style: normal; }

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato/Lato-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato/Lato-Regular.woff") format("woff"), url("../fonts/Lato/Lato-Regular.ttf") format("truetype"), url("../fonts/Lato/Lato-Regular.svg#Lato-Regular") format("svg");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Lato';
  src: url("../fonts/Lato/Lato-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Lato/Lato-Bold.woff") format("woff"), url("../fonts/Lato/Lato-Bold.ttf") format("truetype"), url("../fonts/Lato/Lato-Bold.svg#Lato-Bold") format("svg");
  font-weight: 700;
  font-style: normal; }

@keyframes mind {
  0% {
    transform: rotateZ(-5deg) translate3d(0, -15px, 0); }
  25% {
    transform: rotateZ(0deg); }
  50% {
    transform: rotateZ(-5deg) translate3d(0, 20px, 0); }
  75% {
    transform: rotateZ(0deg); }
  100% {
    transform: rotateZ(-5deg) translate3d(0, -15px, 0); } }

@keyframes wave-1 {
  0% {
    transform: rotateZ(0deg) translate(20px, -8px) rotateZ(0deg); }
  100% {
    transform: rotateZ(360deg) translate(20px, -8px) rotateZ(-360deg); } }

@keyframes wave-2 {
  0% {
    transform: rotateZ(0deg) translate(-15px, 5px) rotateZ(0deg); }
  100% {
    transform: rotateZ(-360deg) translate(-15px, 5px) rotateZ(360deg); } }

@keyframes wave-3 {
  0% {
    transform: rotateZ(0deg) translate(-25px, 15px) rotateZ(0deg); }
  100% {
    transform: rotateZ(-360deg) translate(-25px, 15px) rotateZ(360deg); } }

@keyframes iceberg-1 {
  0% {
    transform: rotateZ(-3deg) translate(25px, 8px); }
  50% {
    transform: rotateZ(8deg) translate(-25px, -5px); }
  100% {
    transform: rotateZ(-3deg) translate(25px, 8px); } }

@keyframes iceberg-2 {
  0% {
    transform: rotateZ(-5deg) translate3d(0, 15px, 0); }
  50% {
    transform: rotateZ(8deg) translate3d(20px, -20px, 0); }
  100% {
    transform: rotateZ(-5deg) translate3d(0, 15px, 0); } }

@keyframes iceberg-3 {
  0% {
    transform: translate(0, 0) rotateZ(0deg); }
  33.3% {
    transform: translate(40px, 0) rotateZ(6deg); }
  66.6% {
    transform: translate(-30px, -5px) rotateZ(-6deg); }
  100% {
    transform: translate(0, 0) rotateZ(0deg); } }
